<template>
    <div class="login-wrap">
        <div class="ms-login">
            <div class="ms-title">智能化共享租赁平台</div>
            <el-form :model="param" :rules="rules" ref="login" label-width="0px" class="ms-content">
                <el-form-item prop="userName">
                    <el-input v-model="param.userName" placeholder="userName">
                        <el-button slot="prepend" icon="el-icon-lx-people"></el-button>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input
                        type="password"
                        placeholder="password"
                        v-model="param.password"
                        @keyup.enter.native="submitForm()"
                    >
                        <el-button slot="prepend" icon="el-icon-lx-lock"></el-button>
                    </el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm()">登录</el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            param: {
                userName: 'Simple123',
                password: '111',
            },
            rules: {
                username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
                password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
            },
        };
    },
    methods: {
        submitForm() {
            this.$refs.login.validate(valid => {
                if (valid) {
                    this.$http.post('/login',this.param)
                    .then((data)=>{
                        console.log(data.data);
                        if(data.data.status == '200'){
                            localStorage.setItem('userId', data.data.userId);
                            localStorage.setItem('ms_username', this.param.userName);
                            data.data.roles.forEach(item=>{
                                if(item.roleName == '顾客'){
                                    this.$message.success('登录成功');
                                    localStorage.setItem('role', item.roleName);
                                }
                            })
                            data.data.roles.forEach(item=>{
                                if(item.roleName == '商家'){
                                    this.$message.success('登录成功');
                                    localStorage.setItem('role', item.roleName);
                                }
                            })
                            data.data.roles.forEach(item=>{
                                if(item.roleName == '管理员'){
                                    this.$message.success('登录成功');
                                    localStorage.setItem('role', item.roleName);
                                }
                            })
                            this.$router.push('/');

                        }else if(data.data.status == '500' || data.data.status == '404'){
                            this.$message.error('账号或密码错误请重新登录！');
                        }else if(data.data.status == '403' || data.data.status == '402'){
                            this.$message.error('账号或密码为空，请重新输入！');
                        }else{
                            this.$message.error('不知名错误，请联系管理员！');
                        }
                    })
                    .catch((err)=>{
                        console.log(err);
                    })

                } else {
                    this.$message.error('请输入账号和密码');
                    console.log('error submit!!');
                    return false;
                }
            });
        },
    },
};
</script>

